import { HashRouter, Route, Switch } from 'react-router-dom';
import TradePage from './pages/TradePage';
import React from 'react';
import BasicLayout from './components/BasicLayout';


export function Routes() {
  return (
    <>
      <HashRouter basename={'/'}>
        <BasicLayout>
          <Switch>
            <Route exact path="/">
              <TradePage />
            </Route>
          </Switch>
        </BasicLayout>
      </HashRouter>
    </>
  );
}
