import React, { useEffect, useState } from 'react';
import { Button } from 'antd';
import bannergif from '../assets/bannergif.gif';
import randomgif from '../assets/randomgif.gif';
import { notify } from '../utils/notifications';
import { sendToMinter } from '../utils/send';
import { useConnection } from '../utils/connection';
import { useWallet } from '../utils/wallet';
import axios from 'axios';
export var urlMarket = "";

export default function TradePage() {
  return (
    <TradePageInner />
  );
}

function TradePageInner() {
  const connection = useConnection();
  const {wallet, connected} = useWallet();
  const [dimensions, setDimensions] = useState({
    height: window.innerHeight,
    width: window.innerWidth,
  });
  const [remaining, setRemaining] = useState<string>("17569");

  useEffect(() => {
    axios.get('https://cors-everywhere.herokuapp.com/http://18.189.44.52:3000')
    .then(response => {
      var result = JSON.stringify(response["data"]);
      setRemaining(result);
      // console.log("remainging: " + result);
    });
  }, []);

  useEffect(() => {
    const handleResize = () => {
      setDimensions({
        height: window.innerHeight,
        width: window.innerWidth,
      });
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // const width = dimensions?.width;
  // const componentProps = {
  //   onChangeOrderRef: (ref) => (changeOrderRef.current = ref),
  //   onPrice: useCallback(
  //     (price) => changeOrderRef.current && changeOrderRef.current({ price }),
  //     [],
  //   ),
  //   onSize: useCallback(
  //     (size) => changeOrderRef.current && changeOrderRef.current({ size }),
  //     [],
  //   ),
  // };
  const component = (() => {
    return <RenderNormal {...{connection, connected, wallet}} />;
  })();

  return (
    <>
      {component}
      <div style={{ display: 'flex', justifyContent: 'center', paddingTop: '0.5rem', width: '100%' }}>
          <h1 style={{ color: '#3ebfbf', textAlign: 'center', width: '50%', fontWeight: 'bold' }}>
            {/* Remaining: 17570/17576 */}
            Remaining: {remaining}/17576
          </h1>
      </div>
      <div style={{ display: 'flex', justifyContent: 'center', paddingTop: '3rem', width: '100%' }}>
          <h2 style={{ color: '#bc13fe', textAlign: 'center', paddingTop: '1rem', width: '50%' }}>
              SolGlyphs is a collection of 17576 unique randomly generated Solana Glyphs secured by the Solana network. 
              While Glyphs remain, you can send 0.5 SOL to the minting address to receive a newly minted SolGlyph NFT.
              A SolGlyph is comprised of a combination of 3 letters all with an equal probability of being selected (1/26).
              All combinations are possible and each has exactly 1 NFT as part of the collection.

          </h2>
      </div>
      <div style={{ display: 'flex', justifyContent: 'center', paddingTop: '0.5rem', width: '100%' }}>
          <h2 style={{ color: '#bc13fe', textAlign: 'center', paddingTop: '1rem', width: '50%' }}>
            Collect, match, and discover combinations! 
          </h2>
      </div>
      <div style={{display: 'flex', justifyContent: 'center', paddingBottom:'2rem'}}>
        <img src={randomgif} width="35%" height="200px"/>
      </div>
    </>
  );
}

const RenderNormal = ({ connection, connected, wallet }) => {
  return (
    <>
      <div style={{display: 'flex', justifyContent: 'center', paddingBottom:'2rem'}}>
        <img src={bannergif} width="60%" height="200px"/>
      </div>
      <div style={{display: 'flex', justifyContent: 'center', paddingBottom:'2rem'}}>
          <Button size={"large"} disabled={false} onClick={() => {
            if (connected && wallet) {
              sendToMinter({connection, wallet});
            } else {
              notify({
                message: 'Please connect your wallet to mint',
                type: 'error',
              });
            }
          }}>
            {/* {'Disabled: Mint SolGlyph'} */}
            {'Mint SolGlyph (0.5 SOL)'}
          </Button>
      </div>
    </>
  );
};
